@import "_mixins/font-face";

// Segoe UI Font
@font-face {
  font-family: 'Segoe UI';
  src: url('SegoeUI-Light.eot');
  src: local("Segoe UI Light"), local("SegoeUI-Light"), url('SegoeUI-Light.eot?#iefix') format("embedded-opentype"), url('SegoeUI-Light.woff') format("woff"), url('SegoeUI-Light.ttf') format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Segoe UI';
  src: url('SegoeUI-BoldItalic.eot');
  src: local("Segoe UI Bold Italic"), local("SegoeUI-BoldItalic"), url('SegoeUI-BoldItalic.eot?#iefix') format("embedded-opentype"), url('SegoeUI-BoldItalic.woff') format("woff"), url('SegoeUI-BoldItalic.ttf') format("truetype");
  font-weight: bold;
  font-style: italic;
  font-display: swap; }

