::placeholder {
	color: #FFF; }

::selection {
	background-color: $accent;
	color: #fff; }

body {
	font-size: 16px;
	min-width: 320px;
	margin-top: 100px;
	position: relative;
	line-height: 1.65;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	background-image: url('../img/src/map.png');
	background-attachment: fixed;
	&.main-page {
		margin-top: 77px;
		.container-md {
			max-width: 960px; }
		.tab_content {
			.container {
				max-width: 960px; } } } }

input, textarea {
	border: #666 1px solid;
	outline: none;
	&:focus:required:invalid {
		border-color: red; }
	&:required:valid {
		border-color: green; } }

input, select, textarea {
	border-radius: .5rem !important; }

h1, h2, h3, h4, h5, h6 {
	font-weight: 600; }

button {
	outline: none !important; }

.visually-hidden {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */;
	clip: rect(1px, 1px, 1px, 1px);
	padding: 0 !important;
	border: 0 !important;
	height: 1px !important;
	width: 1px !important;
	overflow: hidden; }

.white-space-nowrap {
	white-space: nowrap; }

.height {
	min-height: 100vh; }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
	&.img-width {
		width: 100%; } }

.text-red {
	color: #FF0000; }

.text-gray {
	color: #333; }

.bg-red {
	background-color: $accent; }

.font-weight-semibold {
	font-weight: 600; }

.filter-gray {
	filter: grayscale(1);
	opacity: .5; }

// ==== NAVIGATION ====
.nav-link {
	color: #fff !important;
	font-size: 12px;
	font-weight: 600; }
// .nav-item.push-top
// 	position: relative
// 	bottom: 5px
// 	word-spacing: 1px
.unlim-search {
	padding: 5px 15px 5px 30px;
	border-radius: 30px;
	background-color: #DF0000;
	color: #FFF;
	border: none;
	background-image: url('../img/icons/search.svg');
	background-repeat: no-repeat;
	background-size: 15px;
	background-position: 10px 10px;
	&::placeholder {
		color: rgba(#FFF, 0.5); } }

.unlim-btn {
	color: $accent;
	background-color: #fff;
	padding: 5px 15px;
	margin: 0 0.5rem;
	border-radius: 16px;
	font-weight: bold;
	text-decoration: none;
	border: 1px solid #FFF;
	transition: all 0.2s linear;
	&:hover {
		color: #fff;
		background-color: #d40000;
		text-decoration: none;
 } }		// box-shadow: 0 5px 10px rgba(0,0,0,0.16)

.search-btn, .user-btn {
	width: 20px;
	cursor: pointer; }

// ==== SEARCH BLOCK ====
.search-block {
	position: fixed;
	left: 100%;
	top: 0;
	width: 100%;
	height: 77.5px;
	background-color: rgba($accent, 1);
	z-index: 9999;
	.search-wrapper {
		display: flex;
		padding: 22px 0;
 }		// justify-content: center
	.search-close {
		background-color: transparent;
		border: none;
		outline: none;
		z-index: 9999;
		img {
			width: 32px; } }
	.search-form {
		width: 100%;
		// display: flex
		// justify-content: space-between

		input {
			width: 100%;
			height: 100%;
			font-size: 20px;
			background-color: transparent;
			border: none;
			letter-spacing: 1px;
			color: #fff;
			&::placeholder {
				color: #ffcccc; }
			&::selection {
				background-color: #fff;
				color: $accent; } } } }

// ==== CAROUSEL ====
.carousel-caption {
	bottom: 65px;
	.carousel-title {
		.red-element {
			display: block;
			width: 60px;
			height: 5px;
			background-color: $accent;
			padding-bottom: 0px; }
		h1 {
			margin-top: 10px;
			position: relative;
			font-size: 52px;
			text-align: left;
			font-weight: 600; } } }
.carousel-indicators {
	li.active {
		background-color: $accent; } }
.text-center {
	text-align: center; }

.red-line {
	display: block;
	height: 5px;
	width: 60px;
	// margin-bottom: 10px
	background-color: $accent; }

