// DASHBOARD
.dashboard-content {
	justify-content: space-between;
	flex-wrap: wrap;
	flex-direction: row; }

.dashboard {
	.flex-item {
		margin-bottom: 20px;
		&-top {
			margin-bottom: 12px;
			&-title {
				font-size: 49px;
				span {
					font-size: 21px; } }
			&-select {
				width: 150px;
				padding: 8px 20px;
				border-radius: 20px;
				outline: none; }
			.custom-margin {
				font-size: 33px; } }
		&-center {
			&-title {
				font-size: 16px;
				line-height: 0; }
			&-progress {
				max-width: 150px;
				height: 13px;
				border: 1px solid $accent;
				border-radius: 30px;
				margin-top: -15px;
				&-fill {
					background-color: $accent;
					height: 100%;
					width: 0;
					border-radius: 30px; } }
			&-button {
				display: block;
				text-align: center;
				background-color: $accent;
				padding: 0px 50px;
				font-size: 26px;
				border: none;
				border-radius: 15px;
				color: #FFF;
				text-decoration: none; } }
		&-bottom {
			&-title {
				font-size: 10px;
				line-height: 0; } } } }
.sale-card {
