.w-22 {
	width: 22px!important; }
.w-38 {
	width: 38%!important; }
.mw-190 {
	max-width: 190px!important; }
.mw-265 {
	min-width: 265px!important; }

.input-inline-icon {
	background-color: #fff;
	cursor: pointer;
	// border-left: none
	img {
		width: 16px;
		height: 16px; } }

.cooperation-content {
	align-items: flex-end;
	margin-top: 2rem;
	img {
		width: 100%; } }

.drowdown-icon {
	width: 10px;
	height: auto;
	margin-left: 3px; }

// Update bootstrap dropdown toggle icon
.dropdown-toggle::after {
	display: none; }

.close-instructions {
	display: block;
	text-decoration: none;
	cursor: pointer;
	&:hover {
		text-decoration: none; }

	img {
		width: 30px;
		height: auto; } }

#close-instructions {
	display: none; }
.extra-bar-toggle {
	display: none; }
#contact-information {
	display: none; }

// Mobile apps
.app_grid {
	display: flex;
	.apple_btn, .play_store_btn {
		position: relative;
		color: #fff;
		background-color: #FF0000;
		border: 1px solid #fff;
		border-radius: 10px;
		height: 75px;
		margin-right: 20px;
		max-width: 220px;
		width: 100%;
		text-align: left;
		line-height: normal;
		-webkit-transition: all 0.3s ease;
		-o-transition: all 0.3s ease;
		transition: all 0.3s ease;
		// &:hover
		// 	border: 1px solid #aaa
		span {
			color: #fff;
			margin-right: 5px;
			&.title {
				color: #fff; } }
		.icon {
			font-size: 36px;
			float: left;
			padding: 6px 0;
			margin-right: 10px;
			margin-left: 12px;
			img {
				display: block;
				width: 36px; } }
		.title {
			display: block;
			font-size: 16px;
			font-family: $default-font;
			margin-top: 3px; }
		.subtitle {
			font-size: 13px;
			font-family: $default-font; } }

	.play_store_btn {
		margin-right: 0;
		.icon {
			padding: 6px 0;
			img {
				width: 36px; } } } }

.footer-bottom {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between; }

#appStoreModal {
	.modal-header, .modal-footer {
		border: none; } }

.border-right-none {
	.form-control {
		border-right: 0!important;
		border-top-right-radius: 0!important;
		border-bottom-right-radius: 0!important; }
	.input-group-text {
		border-top-right-radius: .5rem!important;
		border-bottom-right-radius: .5rem!important; } }


@media screen and ( max-width: 326px ) {
	.strike {
		&::before {
			left: -10px !important;
			margin-top: 46px;
			width: 100px;
			transform: rotate(36deg); } } }
