}


.link-user {
    // background-color:blue;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    border: 1.5px solid transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    transition: all 0.2s linear;

    &:hover {
        border: 1.5px solid white;
    }
}


.ni-li,
.search-btn {
    transition: all 0.1s linear;

    &:hover {
        transform: scale(1.1);
    }
}


.carousel-caption {
    .carousel-title {
        h1 {
            font-size: 45px !important;
        }
    }
}


//////// NEW STYLE
#search-results {
    display: none;
}

#country-select,
#region-select {
    display: none;
}


.rotate--45 {
    transform: rotateZ(-45deg);
}

.modal-add {
    .modal-dialog {
        .modal-content {
            border-radius: 15px !important;
            background: linear-gradient(rgb(163, 159, 159), rgb(48, 48, 48));

            .modal-header {
                padding-top: 25px;
                border: none !important;

                .modal-title {
                    text-transform: uppercase;

                    text-align: center !important;
                    width: 100%;
                    color: white !important;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    align-content: center;

                    .modal-line {
                        width: 50px;
                        height: 2px;
                        background-color: red;
                    }
                }
            }

            .modal-body {
                padding: 20px 35px;

                .under-footer {
                    border-radius: 15px;
                    border: 1px solid white;
                    padding: 10px;

                    ul {
                        list-style: none;
                        padding: 0;
                        margin: 0;
                        padding-left: 10px;
                    }

                }

                ul {
                    padding-left: 20px;

                    li {
                        color: white;
                        font-size: 14px;
                    }
                }

                .title-box {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    align-content: center;

                    p {
                        color: white;
                        font-size: 17px;
                        font-weight: 500;
                        padding: 0;
                        margin: 0;

                    }

                    span.tit-line {
                        width: 70%;
                        height: 1px;
                        background-color: #fff;
                    }
                }
            }
        }
    }
}



.map-filter-input {
    border: none !important;
    box-shadow: 0px 3px 6px rgba(31, 30, 30, 0.161);
    background: #FFFFFF 0% 0% no-repeat padding-box;
}

.map-country-list {
    li {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
}

.nav-link {
    position: relative;

    &:hover {
        span.line-top {
            opacity: 1;
        }
    }

    span.line-top {
        transition: all 0.2s linear;
        position: absolute;
        opacity: 0;
        width: 25px;
        height: 1.5px;
        background-color: #fff;
        top: 3px;
    }
}

