li.let {
    color: rgb(212, 0, 0);
    pointer-events: none;
}

.under-nav {
    position: absolute;
    top: 100%;
    width: 100%;
    left: 0;
    background-color: rgb(212, 0, 0);
    display: none;

    .container {
        padding-left: 207px;
        padding-bottom: 0 !important;
        margin-bottom: 0 !important;

        @media all and (max-width:992px) {
            padding: 0;

        }

        ul {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            align-content: center;

            li {
                a {
                    font-size: 13px;
                }

                transition: all 0.1s linear;

                &:hover {
                    transform: scale(1.1) !important;
                }
            }
        }
    }
}


