                    align-content: center;

                    p {
                        text-align: center;
                        width: 52px;
                    }
                }
            }

            transition: all 0.2s linear;
            border: 2px solid transparent;

            &:hover {
                border: 2px solid white;

                button.btn-buy {
                    text-decoration: none !important;
                    background-color: red;
                    color: white !important;
                    border-color: red;
                }
            }
        }
    }
}