
.tab_content-map {
  border-bottom: 1px solid #919196;
  box-shadow: inset 0 8px 9px -8px rgba(0,0,0,0.7);
  background-color: #fff; }
.map-bg {
  width: 100%;
  padding-bottom: 50px;
  position: relative;
  z-index: 2;
  min-height: 500px;

  .container {
    position: relative;
    z-index: 3; } }

.map-bg-box {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-image: url("./../img/src/world-wide.png");
  background-repeat: no-repeat;
  background-position: left 15px center;
  background-size: contain;
  z-index: 1; }

.map-bg-box--oceania {
 }  // background-size: cover

// .map-bg-box--oceania
//     background-position: left -12% center
// .map-bg-box--africa,
// .map-bg-box--asia
//   background-position: left -9% center
.map-bg-box--europe {
  background-position: right center; }
.map-bg-box--samerica {
  background-position: left 20px center; }

.mab-bg__title {
  padding-top: 40px;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  h3 {
    font-size: 24px; }

  span {
    display: block;
    margin-bottom: 5px;
    width: 40px;
    height: 2px;
    background-color: #ff0000; } }


.countries-box {
  border: 1px solid #707070;
  border-right: none;
  border-left: none;
  display: flex;
  flex-wrap: wrap;
  padding: 25px 15px 10px 15px;
  overflow-y: auto;
  list-style-type: none;
  width: 85%;
  max-height: 500px;
  margin: 0 auto;
  background: transparent;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  img {
    margin-right: 10px;
    object-fit: cover; }
  li {
    margin-bottom: 16px;
    width: calc( (100% - 39px) / 3);
    font-size: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-right: 10px;
    transition: 0.3s ease;
    border-bottom: 1px solid transparent;
    &:hover,&:focus {
      color: #ff0000; }
    span {
      text-transform: capitalize; } }

  scrollbar-width: thin;

  &::-webkit-scrollbar {
    width: 9px; }

  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: grey;
    outline: 1px solid transparent; } }



@media screen and ( max-width: 1670px ) {
  // .map-bg
  //   width: 100%
  //   padding-bottom: 50px
  //   position: relative
  //   z-index: 2
  //   min-height: 660px

  .countries-box {
    width: 540px;
    li {
      width: calc( (540px - 39px) / 3);
      font-size: 14px; }
    img {
      width: 22px;
      height: 15px; } } }


@media screen and ( max-width: 850px ) {
  .countries-box {
    width: 420px;
    li {
      width: calc( (420px - 40px) / 2);
 }      // font-size: 14px
