@media screen and ( max-width: 650px ) {
  .map-bg {
    min-height: auto; }
  .countries-box {
    position: relative;
    width: 70%;
    background-image: url("./../img/src/world-wide-m.png");
    li {
      width: 100%; }

    scrollbar-width: thin;

    &::-webkit-scrollbar {
      width: 3px; }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: grey;
      outline: 1px solid transparent; } }
  .map-bg-box {
    background-image: none !important; } }
